import { Injectable } from "@angular/core";
import { WorkFlow } from "./types";

@Injectable()
export class WorkFlowMessageService {
  constructor() { }
  /**
   * 
   * @param callback 
   */
  public listen(callback: (e: WorkFlow.IMessage) => void) {
    window.addEventListener('message', function (event) {
      var message = event.data as WorkFlow.IMessage;
      if (message) {
        message.sender = event.source;
      }
      if (typeof callback === 'function') {
        callback(message);
      }
    });
  }
  /**
   * 
   * @param message 
   */
  public send(message: WorkFlow.IMessage): void {
    if (!message) {
      return;
    }
    let win: Window = window;
    if (message.target) {
      win = message.target as Window || window;
      delete message.target;
    }
    if (win) {
      win.postMessage(message, '*');
    }
  }
}