import { Injectable, InjectionToken, Inject, Optional } from '@angular/core';
export const FARRIS_FORM_COMPONENTS = new InjectionToken<{[key: string]: any}>('');

/**
 * 
 * @scope FormModule
 */
@Injectable()
export class ComponentManagerService {

    private controls: { [key: string]: any} = {};

    appendControl(key: string, cmpIns: any) {
        this.controls[key] = cmpIns;
    }

    getControl(key: string) {
        if (this.controls[key]) {
            return this.controls[key];
        } else {
            console.warn(`Key${key}`);
            return null;
        }
    }

}
