import { Injectable } from '@angular/core';
import { QuerystringService } from './querystring';
import { RuntimeFrameworkService } from './rtf-service';
import { of, Observable, BehaviorSubject, Subject } from 'rxjs';
import { switchMap } from 'rxjs/operators';
// tslint:disable: no-string-literal
/**
 * 
 * @scope FormModule
 */
@Injectable()
export class ParamService {
    constructor(
        private querystringService: QuerystringService,
        private runtimeFrameworkService: RuntimeFrameworkService
    ) { }
    /**
     * 
     */
    public get params(): Observable<{ [propName: string]: any }> {
        // hash
        const hash = window.location.hash;
        const params = this.querystringService.parse(hash);
        /*
        const appMode = params.appMode;
        // appMode,appMode = mdi  spa
        if (typeof appMode === 'undefined' || appMode === null) {
            return of(params);
        }
        // appMode
        if (appMode.length < 1) {
            return of(params);
        }
        // iframe
        if (appMode.toLowerCase() === 'mdi') {
            return of(params);
        }
        */
        // tabIdtabIdtabIdurl
        const tabId = params.tabId;
        if (!tabId) {
            // throw new Error('TabId can`t be empty!');
            return of(params);
        }
        const subject = new Subject<any>();
        // eobjectMap
        this.runtimeFrameworkService.addEventListener(tabId, (e: any) => {
            let map = {};
            if (e instanceof Map || (e && typeof (e['get']) === 'function' && typeof e['entries'] === 'function')) {
                map = this.parseMapParams(e);
            }
            setTimeout(() => {
                subject.next(Object.assign({}, e, map, params));
            }, 0);
        }, params);
        return subject.asObservable();
    }
    /**
     * map
     * @param params params
     */
    private parseMapParams(params: Map<any, any>) {
        const map = {};
        map['WEB_FORM_ROUTE_PARAMS'] = decodeURIComponent(params.get('WEB_FORM_ROUTE_PARAMS'));
        params.forEach((value, key) => {
            if (key !== 'WEB_FORM_ROUTE_PARAMS') {
                map[key] = value;
            }
        });
        return map;
    }
    /**
     * param
     * @param param key
     */
    public get(param: string): Observable<any> {
        return this.params.pipe(
            switchMap(options => {
                if (!!options && options.hasOwnProperty(param)) {
                    return of(options.param);
                } else {
                    return of(undefined);
                }
            })
        );
    }
    /**
     * 
     */
    public parse(): Observable<any> {
        return this.params.pipe(
            switchMap(options => {
                return of(options);
            })
        );
    }
}
